<template>
    <div ref="chartRef" class="chart-container" style="width: 100%; height: 400px;"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts'
  
  export default {
    name: 'ChartComponent',
    props: {
      completedCount: Number,
      totalCount: Number
    },
    mounted() {
      this.initChart();
    },
    watch: {
      completedCount: 'updateChart',
      totalCount: 'updateChart'
    },
    methods: {
      initChart() {
        const chartDom = this.$refs.chartRef;
        const myChart = echarts.init(chartDom);
        myChart.setOption({
          title: {
            text: 'To-Do List Progress'
          },
          tooltip: {},
          xAxis: {
            type: 'category',
            data: ['Completed', 'Pending']
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [this.completedCount, this.totalCount - this.completedCount],
            type: 'bar',
            showBackground: true,
            backgroundStyle: {
              color: 'rgba(180, 180, 180, 0.2)'
            }
          }]
        });
        this.myChart = myChart;
      },
      updateChart() {
        if (this.myChart) {
          this.myChart.setOption({
            series: [{
              data: [this.completedCount, this.totalCount - this.completedCount]
            }]
          });
        }
      }
    },
    beforeUnmount() {
      if (this.myChart) {
        this.myChart.dispose();
      }
    }
  }
  </script>
  
  <style scoped>
  .chart-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
  }
  </style>